.eventCorner{
    height: 90vh;
    width: 100%;
    margin-top: 8vh;
}

.eventCorner-heading{
    font-weight: bold;
    text-align: center;
}

.events-content-collection{
    margin-top: 10vh;
    justify-content: center;
    align-content: center;
    display: flex;
}

.events-content{
    padding-left: 1vh;
    position: relative;
    width: 33%;
}

.events-content img{
    height: 60vh;
    width: 100%;
    opacity: 1;
    transition: .5s ease;
    backface-visibility: hidden;
}

.event-content-heading{
    position: absolute;
    text-align: center;
    width: 100%;
    padding-left: 10%;
    padding-right: 10%;
    top: 70%;
    color: white;
    font-weight: bold;
}

.event-content-detail{
    width: 70%;
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
    color: white;
}

.events-content-img:after{
    position:absolute;
    width:100%; height:100%;
    top:0; left:0;
    background:rgba(0,0,0,0.6);
    opacity:0;
    transition: all 0.5s;
    -webkit-transition: all 0.5s;
}

.events-content:hover .event-content-heading{
    opacity: 0;
}

.events-content:hover .events-content-img{
    filter: brightness(50%);
}
  
.events-content:hover .event-content-detail{
    opacity: 1;
}