.ourPrograms{
    margin-top: 5vh;
}

.ourProgramsGridCollection{
    margin-top: 10vh;
    width: 90vw;
    height: 70vh;
    display: flex;
    justify-content: center;
    position: relative;
    margin-left: auto;
    margin-right: auto;
}

.programSingleGrid{
    width: 30%;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.programSingleGrid-img{
    height: 65vh;
    width: auto;
    position: absolute;
    z-index: -1;
    margin-left: 50px;
    display: block;
    opacity: 1;
    transition: .5s ease;
    backface-visibility: hidden;
}

.programSingleGrid h2{
    color: white;
    margin-top: 80%;
    margin-right: 25%;
    margin-left: 25%;
}

.programSingleGrid:hover .programSingleGrid-img {
    opacity: 0.3;
  }
  
  .container:hover .programName-hovereffect {
    opacity: 1;
  }

.programName-hovereffecttext{
    background-color: #4CAF50;
    color: black;
    font-size: 16px;
}

.programName-hovereffect{
    transition: .5s ease;
    opacity: 0  ;
    text-align: center;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    z-index: 99;
    justify-content: center;
    align-items: center;
}