* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

body {
	font-family: 'Open Sans', sans-serif;
	line-height: 1.5;
}

.showcase {
	height: 100vh;
	display: flex;
	justify-content: center;
	text-align: center;
	color: #fff;
	padding: 0 20px;
}

.video-container {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	overflow: hidden;
}

.video-container video {
	min-width: 100%;
	min-height: 100%;
  	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	object-fit: cover;
}

.video-container:after {
	content: '';
	z-index: 1;
	height: 100%;
	width: 100%;
	top: 0;
	left: 0;
	background: rgba(0, 0, 0, 0.5);
	position: absolute;
}

.content{
	z-index: 2;
	line-height: 2em;
	margin-top: 55vh;
	background-color: rgba(0, 0, 0, 0.35);
	padding-top: 12vh;
	padding-left: 10vh;
	padding-right: 10vh;
	padding-bottom: 12vh;
	max-height: 35vh;
}

.edjusticeColor{
	color: #E05B0D;
}

h1,p{
	display: inline-block;
}

.content h1{
	font-size: 5.5em;
	font-family: 'arial black',arial-w01-black,arial-w02-black,'arial-w10 black',sans-serif;	
}

p{
	font-size: 24px;
	font-weight: bold;
}

.tagline{
	float: right;
}