.edjusticeintro{
    text-align: center;
    padding-top: 10vh;
    padding-bottom: 10vh;
}

.edjusticeintro h2{
    font-size: 2.5em;
    font-weight: bold;
    line-height: 80%;
}

.heading{
    padding-bottom: 8vh;
    text-align: center;
}

.edjustice-edjusticeintro{
    margin-left: -40vh;
}

.tagline-edjusticeintro{
    margin-left: 20vh;
    font-size: 2em !important;
}

.introcontent{
    padding: 0 25vh;
    padding-bottom: 5vh;
}
.viewmore-edjusticeintro{
    font-size: 1.1em;
    padding: 1vh 3vh;
    cursor: pointer;
    border: solid rgba(50, 50, 50, 0.44) 2px;
    border-radius: 20px;
    background-color: transparent;
}

.viewmore-edjusticeintro:hover{
    background-color: rgba(0, 0, 0, 0.8);
    color: white;
    transition: border-color 0.4s ease 0s, background-color 0.4s ease 0s;
}